import { useEffect, useState } from "react"
import { useChatStore } from "../Store/useChatStore.js"
import SidebarSkeleton from "./Skeletons/SidebarSkeleton.jsx"
import { Users } from "lucide-react"
import { useAuthStore } from "../Store/useAuthStore.js"

const Sidebar = () => {

  const {getUsers, users, isUsersLoading, setSelectedUser, selectedUser} = useChatStore()
  
  const { onlineUsers } = useAuthStore()
  // console.log("Users online",onlineUsers)
  const [showOnlineUsers, setShowOnlineUsers ] = useState(false)

  useEffect(()=> {
    getUsers()
  }, [getUsers])

  const filteredUsers = showOnlineUsers ? users.filter((user)=> onlineUsers.includes(user)) : users

  if(isUsersLoading) return( <SidebarSkeleton/>)

  return (
    <div className="h-full w-20 lg:w-72 border-r border-base-300 flex flex-col transition-all duration-100">
      <div className="border-b border-base-300 w-full p-5">
        <div className="flex items-center gap-2">
          <Users className="size-6"/>
          <span className="font-medium hidden lg:block">Contacts</span>
        </div>
        {/** To-do Online filter */}

          <div className="mt-3 hidden lg:flex items-center gap-2">
              <label className="cursor-pointer flex items-center gap-2">
                <input type="checkbox" checked={showOnlineUsers} 
                onChange={(e) => setShowOnlineUsers(e.target.value)} className="checkbox checkbox-sm"/>
                <span className="text-sm"> Show Online only</span>
              </label>
              <span className="text-sm text-zinc-500">( {onlineUsers.length - 1} online)</span>
          </div>

        {/** name of users */}
         <div className="overflow-y-auto w-full py-3">
            {filteredUsers.map((user) => {
              return (<button 
              key={user._id}
              onClick={()=> setSelectedUser(user)}
              className={`w-full p-3 flex items-center gap-3
              hover:bg-base-300 transition-colors 
              ${ selectedUser?._id === user._id ? "bg-base-300 ring-1 ring-base-300" : ""}
              `}>

                <div className="relative mx-auto lg:mx-0">
                  <img src={user.profilePic || "/avatar.png"} 
                  alt={user.fullName} 
                  className="size-12 object-cover rounded-full"/>
                  {onlineUsers.includes(user._id) && (
                    <span className="absolute bottom-0 right-0 size-3 bg-green-500
                    rounded-full ring-2 ring-zinc-900"/>
                  )}
                </div>

                {/** User info only available on larger screen */}
                  <div className="hidden lg:block text-left min-w-0">
                    <div className="font-medium truncate">{user.fullName}</div>
                    <div className="text-sm text-zinc-400">
                      {onlineUsers.includes(user._id) ? "Online" : "Offline"}
                    </div>
                  </div>
              </button>)
            })}
         </div>
      </div>
    </div>
  )
}

export default Sidebar